import React, { useState, useEffect } from 'react';
import './NinjaSquadContainer.css';

interface NinjaSquadContainerProps {
  children: React.ReactChild[];
}

function NinjaSquadContainer(props: NinjaSquadContainerProps) {
  const NUMBER_PER_PAGE = 40;
  const [quantity, setQuantity] = useState(NUMBER_PER_PAGE);

  // reset pagination if another set of children rendered
  useEffect(() => { 
    setQuantity(NUMBER_PER_PAGE);
  }, [props.children] );

  const hiddenClass = quantity >= props.children.length ? 'hidden' : '';

  return (
    <div className="ninja-squad-container">
      <div className="ninja-squad">
          {props.children.slice(0, quantity)}
      </div>
      <button 
        className={'more-button ' + hiddenClass} 
        onClick={_ => setQuantity(quantity + NUMBER_PER_PAGE)}>
          More 
      </button> 
    </div>
  );
}

export default NinjaSquadContainer;