import {getOfficeFlag} from './util';
import './NinjaCard.css';

interface NinjaCardProps {
  name: string;
  office: string;
  imagePortraitUrl?: string,
  linkedIn?: string;
  gitHub?: string;
  twitter?: string;
}

function NinjaCard(props: NinjaCardProps) {
  const linkedInEl = <a href={"https://linkedin.com" + props.linkedIn} target="_blank" rel="noreferrer"><i className="fab fa-linkedin"></i></a>;
  const gitHubEl = <a href={"https://github.com/" + props.gitHub} target="_blank" rel="noreferrer"><i className="fab fa-github"></i></a>;
  const twitterEl = <a href={"https://twitter.com/" + props.twitter} target="_blank" rel="noreferrer"><i className="fab fa-twitter"></i></a>;

  return (
    <article className="ninja-card">
      <img className="portrait" src={props.imagePortraitUrl ? props.imagePortraitUrl : "assets/ninja.png"} alt={props.name}/>
      <div className="details">
        <h2>
          <span className="name">{props.name}</span><br></br>
          <span className="office">Office: <span className="emoji-font">{getOfficeFlag(props.office)}</span> {props.office}</span>
        </h2>
        <address>
          {props.linkedIn ? linkedInEl : undefined}
          {props.gitHub ? gitHubEl : undefined}
          {props.twitter ? twitterEl : undefined}
        </address> 
      </div>
    </article> 
  );
}

export default NinjaCard;